<template>
  <div>
    <v-card class="rowbg">
      <v-row>
        <v-col cols="4">
          <a href="/">
            <v-icon color="dark" class="btn_navbar">mdi-arrow-left</v-icon>
          </a>
        </v-col>
        <v-col cols="4" class="text-center">
          <h3>Pengenalan Kubus</h3>
        </v-col>
        <v-col cols="4" class="text-end">
          <a href="teori">
            <v-icon color="dark" class="btn_navbar">mdi-arrow-right</v-icon>
          </a>
        </v-col>
      </v-row>
    </v-card>
    <v-row>
      <v-col cols="12" sm="6">
        <center>
          <v-card class="card_soal" elevation="0">
            <h3 class="hsoal marg">Masukkan gambar ke kotak yang tersedia, apakah gambar merupakan "kubus" atau "balok"</h3>
            <center>
              <img v-if="index >= 0 && jawab0 == 0" :src="gambar[0]" class="img_balok" id="0" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 0" class="hsoal marg">{{nama_gambar[0]}}</h3>

              <img v-if="index > 0 && jawab1 == 0" :src="gambar[1]" class="img_balok" id="1" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 1" class="hsoal marg">{{nama_gambar[1]}}</h3>

              <img v-if="index > 1 && jawab2 == 0" :src="gambar[2]" class="img_kubus" id="2" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 2" class="hsoal marg">{{nama_gambar[2]}}</h3>

              <img v-if="index > 2 && jawab3 == 0" :src="gambar[3]" class="img_kubus" id="3" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 3" class="hsoal marg">{{nama_gambar[3]}}</h3>

              <img v-if="index > 3 && jawab4 == 0" :src="gambar[4]" class="img_balok" id="4" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 4" class="hsoal marg">{{nama_gambar[4]}}</h3>

              <img v-if="index > 4 && jawab5 == 0" :src="gambar[5]" class="img_kubus" id="5" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 5" class="hsoal marg">{{nama_gambar[5]}}</h3>

              <img v-if="index > 5 && jawab6 == 0" :src="gambar[6]" class="img_kubus" id="6" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 6" class="hsoal marg">{{nama_gambar[6]}}</h3>

              <img v-if="index > 6 && jawab7 == 0" :src="gambar[7]" class="img_balok" id="7" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 7" class="hsoal marg">{{nama_gambar[7]}}</h3>

              <img v-if="index > 7 && jawab8 == 0" :src="gambar[8]" class="img_kubus" id="8" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 8" class="hsoal marg">{{nama_gambar[8]}}</h3>

              <img v-if="index > 8 && jawab9 == 0" :src="gambar[9]" class="img_balok" id="9" draggable="true" @dragstart="dragstart" @dragover.stop v-on:touchstart="dragtouch">
              <h3 v-if="index == 9" class="hsoal marg">{{nama_gambar[9]}}</h3>

              <div v-if="index == 10">
                <br><br><br>
                <v-btn icon v-on:click="retry">
                  <v-icon size="200px" color="#606060">mdi-refresh</v-icon>
                </v-btn>
                <br><br>
                <h6 class="mt-8">RETRY</h6>
              </div>
            </center>
          </v-card>
        </center>
      </v-col>
      <v-col cols="12" sm="6" class="text-center">
        <v-container>
          <v-row>
            <v-col cols="6" class="text-center">
              <center>
                <v-card class="divdiv1" id="11" @dragover.prevent @drop="drop">
                  <v-card-title class="card_title"><h3 class="hsoal">Kubus</h3></v-card-title>
                  <v-card-text v-if="touchindex == 1">
                    <img src="../assets/pengenalan/upload.png" v-on:click="kubus_touch" class="img_touch">
                  </v-card-text>
                  <img v-if="index_jawab >= 3" :src="gambar[2]" class="img_touch_jawab_k"><br>
                  <img v-if="index_jawab >= 4" :src="gambar[3]" class="img_touch_jawab_k"><br>
                  <img v-if="index_jawab >= 6" :src="gambar[5]" class="img_touch_jawab_k"><br>
                  <img v-if="index_jawab >= 7" :src="gambar[6]" class="img_touch_jawab_k"><br>
                  <img v-if="index_jawab >= 9" :src="gambar[8]" class="img_touch_jawab_k">
                </v-card>
              </center>
            </v-col>
            <v-col cols="6" class="text-center">
              <center>
                <v-card class="divdiv1" id="11" @dragover.prevent @drop="drop1">
                  <v-card-title class="card_title"><h3 class="hsoal">Balok</h3></v-card-title>
                  <v-card-text v-if="touchindex == 1">
                    <img src="../assets/pengenalan/upload.png" v-on:click="balok_touch" class="img_touch">
                  </v-card-text>
                  <img v-if="index_jawab >= 1" :src="gambar[0]" class="img_touch_jawab_b"><br>
                  <img v-if="index_jawab >= 2" :src="gambar[1]" class="img_touch_jawab_b"><br>
                  <img v-if="index_jawab >= 5" :src="gambar[4]" class="img_touch_jawab_b"><br>
                  <img v-if="index_jawab >= 8" :src="gambar[7]" class="img_touch_jawab_b"><br>
                  <img v-if="index_jawab >= 10" :src="gambar[9]" class="img_touch_jawab_b">
                </v-card>
              </center>
            </v-col>
          </v-row>
        </v-container>
      </v-col>
    </v-row>
  </div>
</template>

<script>
  import swal from 'sweetalert2'

  export default {
    data() {
      return{
        gambar: [
          require('../assets/pengenalan/aquarium.png'),
          require('../assets/pengenalan/bed.png'),
          require('../assets/pengenalan/celengan.png'),
          require('../assets/pengenalan/dadu.png'),
          require('../assets/pengenalan/kue.jpg'),
          require('../assets/pengenalan/milo.jpg'),
          require('../assets/pengenalan/rubik.png'),
          require('../assets/pengenalan/striper.png'),
          require('../assets/pengenalan/semongko.jpg'),
          require('../assets/pengenalan/kotak_tisu.png')
        ],
        nama_gambar: [
          "- Aquarium -", "- Kasur -", "- Celengan -", "- Dadu -", "- Kue Coklat -", 
          "- Milo -", "- Rubik -", "- Penghapus -", "- Semangka -", "- Kotak Tisu -"
        ],
        index: 0,
        touchindex: 0,
        index_jawab: 0,
        jawab0: 0, jawab1: 0, jawab2: 0, jawab3: 0, jawab4: 0, jawab5: 0, jawab6: 0, jawab7: 0, jawab8: 0, jawab9 : 0
      }
    },
    methods: {
      dragstart: e => {
        const target = e.target
        e.dataTransfer.setData("card_id", target.id)
        setTimeout(() => {
          target.style.display = "block"
        }, 0)
      },
      drop(e) {
        const card_id = e.dataTransfer.getData("card_id")
        const card = document.getElementById(card_id)
        if (card_id == 2 || card_id == 3 || card_id == 5 || card_id == 6 || card_id == 8) {
          swal.fire({
            icon: 'success',
            title: 'Benar',
            text: "Kamu meletakkan benda pada tempatnya",
            allowOutsideClick: false,
            width: 350,
            backdrop: 'rgba(0,0,0,0.8)',
          }).
          then(res => { 
            card.style.display = "block"
            card.style.width = "40%"
            card.style.height = "20%"
            card.style.marginTop = "40px"
            e.target.appendChild(card)
            this.index += 1
          })
        } else {
          swal.fire({
            icon: 'error',
            title: 'Coba Lagi',
            text: "Kamu belum meletakkan benda pada tempatnya",
            allowOutsideClick: false,
            width: 350,
            backdrop: 'rgba(0,0,0,0.8)',
          })
        }
      },
      drop1(e) {
        const card_id = e.dataTransfer.getData("card_id")
        const card = document.getElementById(card_id)
        if (card_id == 0 || card_id == 1 || card_id == 4 || card_id == 7 || card_id == 9) {
          swal.fire({
            icon: 'success',
            title: 'Benar',
            text: "Kamu meletakkan benda pada tempatnya",
            allowOutsideClick: false,
            width: 350,
            backdrop: 'rgba(0,0,0,0.8)',
          }).
          then(res => {
            card.style.display = "block"
            card.style.width = "60%"
            card.style.height = "20%"
            card.style.marginTop = "40px"
            e.target.appendChild(card)
            this.index += 1
          })
        } else {
          swal.fire({
            icon: 'error',
            title: 'Coba Lagi',
            text: "Kamu belum meletakkan benda pada tempatnya",
            allowOutsideClick: false,
            width: 350,
            backdrop: 'rgba(0,0,0,0.8)',
          })
        }
      },
      retry() {
        location.reload()
      },
      dragtouch(e) {
        this.touchindex = 1
      },
      balok_touch(e) {
        if (this.index == 0 || this.index == 1 || this.index == 4 || this.index == 7 || this.index == 9) {
          swal.fire({
            icon: 'success',
            title: 'Benar',
            text: "Kamu meletakkan benda pada tempatnya",
            allowOutsideClick: false,
            width: 350,
            backdrop: 'rgba(0,0,0,0.8)',
          }).
          then(res => {
            this.index_jawab += 1
            this.touchindex = 0

            if (this.index == 0) this.jawab0 = 1
            if (this.index == 1) this.jawab1 = 1
            if (this.index == 4) this.jawab4 = 1
            if (this.index == 7) this.jawab7 = 1
            if (this.index == 9) this.jawab9 = 1

            this.index += 1
          })
        } else {
          swal.fire({
            icon: 'error',
            title: 'Coba Lagi',
            text: "Kamu belum meletakkan benda pada tempatnya",
            allowOutsideClick: false,
            width: 350,
            backdrop: 'rgba(0,0,0,0.8)',
          })
        }
      },
      kubus_touch(e) {
        if (this.index == 2 || this.index == 3 || this.index == 5 || this.index == 6 || this.index == 8) {
          swal.fire({
            icon: 'success',
            title: 'Benar',
            text: "Kamu meletakkan benda pada tempatnya",
            allowOutsideClick: false,
            width: 350,
            backdrop: 'rgba(0,0,0,0.8)',
          }).
          then(res => {
            this.index_jawab += 1
            this.touchindex = 0

            if (this.index == 2) this.jawab2 = 1
            if (this.index == 3) this.jawab3 = 1
            if (this.index == 5) this.jawab5 = 1
            if (this.index == 6) this.jawab6 = 1
            if (this.index == 8) this.jawab8 = 1

            this.index += 1
          })
        } else {
          swal.fire({
            icon: 'error',
            title: 'Coba Lagi',
            text: "Kamu belum meletakkan benda pada tempatnya",
            allowOutsideClick: false,
            width: 350,
            backdrop: 'rgba(0,0,0,0.8)',
          })
        }
      }
    }
  }
</script>

